<template>
  <div class="nav-collapse">
    <div class="header_data">
      <h2>公司名稱</h2>
      <div class="header_box">
        <img src="@/assets/images/use.png" alt="" class="img-responsive" />
        <div class="header_right">
          <h3>使用者名稱</h3>
          <h4>
            <a href="javascript:;" translate>logout</a>
          </h4>
        </div>
      </div>
    </div>

    <!-- 左側選單開始 -->
    <div id="div-menu">
      <ul ref="menu" class="sidebar-menu">
        <li
          class="sub-menu"
          v-for="(menu, index) in menus"
          :key="index"
          @click="onSubMenuClick"
        >
          <a>
            <i>
              <img
                src="@/assets/images/menu_s_01@2x.png"
                class="img-responsive"
              />
            </i>
            <span>{{ menu.modelName }}</span>
            <span
              class="menu-arrow arrow_carrot-down arrow_carrot-right"
            ></span>
            <i class="fa fa-caret-up"></i>
            <i class="fa fa-caret-down"></i>
          </a>
          <ul class="sub">
            <li v-for="(func, subIndex) in menu.functions" :key="subIndex">
              <a :href="func.url">{{ func.funcName }}</a>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <!-- 左側選單結束 -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      /**
       * Menu 列表
       * @type { MenuModel[] }
       * */
      menus: []
    }
  },
  mounted() {
    this.menus = [
      {
        modelName: 'A模組功能名稱',
        modelIdx: 1,
        modelImage: 'menu_s_01@2x.png',
        functions: [
          {
            funcName: 'A模組功能 - 子功能一',
            url: ''
          },
          {
            funcName: 'A模組功能 - 子功能二',
            url: ''
          }
        ]
      },
      {
        modelName: 'B模組功能名稱',
        modelIdx: 2,
        modelImage: 'menu_s_02@2x.png',
        functions: [
          {
            funcName: 'B模組功能 - 子功能一',
            url: ''
          },
          {
            funcName: 'B模組功能 - 子功能二',
            url: ''
          }
        ]
      },
      {
        modelName: '招募平台',
        modelIdx: 2,
        modelImage: 'menu_s_02@2x.png',
        functions: [
          {
            funcName: '職缺維護',
            url: '/job'
          },
          {
            funcName: 'Banner 維護',
            url: '/banner'
          },
          {
            funcName: 'QR Code 維護',
            url: '/qrCode'
          }
        ]
      }
    ]
  },
  methods: {
    /** 開啟或關閉 menu */
    toggle() {
      $(this.$refs.menu).slideToggle()
    },
    /** 選單點擊時，開啟或關閉子選單 */
    onSubMenuClick({ currentTarget }) {
      $(currentTarget)
        .toggleClass('open')
        .find('.sub')
        .slideToggle()

      $(currentTarget)
        .siblings()
        .removeClass('open')
        .find('.sub')
        .slideUp()
    }
  }
}

/**********
 * 類型定義
 *********/

/**
 * Menu
 * @typedef {Object} MenuModel Menu 主項目
 * @property {string} copCode 關聯公司代碼
 * @property {string} modelCode 模組代碼
 * @property {string} modelName 模組名稱
 * @property {number | null} modelIdx 模組順序
 * @property {string} modelImage 模組圖檔
 * @property {MenuFunction[]} functions 功能清單
 */

/**
 * Menu 項目
 * @typedef {Object} MenuFunction Menu 子項目
 * @property {number | null} funcId 功能流水號
 * @property {string | null} funcName 功能名稱
 * @property {number | null} funcIdx 功能順序
 * @property {boolean} funcEntry 是功能入口
 * @property {string} url 功能連結
 * @property {string | null} frontType 前端類型 (Angular, Vue)
 */
</script>
