import Vue from 'vue'
import VueRouter from 'vue-router'
import Job from '../views/Job.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Job
  },
  {
    path: '/job',
    name: 'job',
    component: () => import(/* webpackChunkName: "job" */ '../views/Job.vue')
  },
  {
    path: '/banner',
    name: 'banner',
    component: () =>
      import(/* webpackChunkName: "banner" */ '../views/Banner.vue')
  },
  {
    path: '/qrCode',
    name: 'qrCode',
    component: () =>
      import(/* webpackChunkName: "qrCode" */ '../views/QRCode.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
