<template>
  <div>
    <div class="title">
      <h3>{{ title }}</h3>
      <nav aria-label="breadcrumb">
        <ol class="breadcrumb">
          <li
            v-for="(item, index) in breadcrumb"
            :key="index"
            :class="{ active: index === breadcrumb.length - 1 }"
            class="breadcrumb-item"
          >
            {{ item.title }}
          </li>
        </ol>
      </nav>
    </div>
    <hr />
  </div>
</template>

<script>
/**
 * Breadcrumb Item
 * @typedef {{ title: string, isActive: boolean | undefined }} BreadcrumbItem
 */

export default {
  props: {
    title: {
      required: true,
      type: String
    },
    /** @type {import('vue').PropOptions<BreadcrumbItem[]>} */
    breadcrumb: {
      type: Array,
      default: () => []
    }
  }
}
</script>
